import "../../App.scss"
import webb from "./web2.png"
import plus from "./plus.png"
import close from "./cc.png"
import { useEffect, useState } from "react"
import penc from "./penc.png"


function Favs() {


    const [name, setName] = useState("")
    const [url, setUrl] = useState("")
    const [storedFavorites, setStoredFavourites] = useState(JSON.parse(localStorage.getItem("favs")))


    const openFavs = () => {
        document.getElementById("add-favs").style.display = "flex"
    }
    const closeFavs = () => {
        document.getElementById("add-favs").style.display = "none"
    }
    const onSubmitFavs = async (e) => {
        e.preventDefault()
        // Step 1: Retrieve existing array from localStorage
        let existingArray = localStorage.getItem("favs");

        // Step 2: Parse the array from JSON representation
        existingArray = existingArray ? JSON.parse(existingArray) : [];

        // Step 3: Push the new object into the array
        existingArray.push({ "name": name, "url": url });

        // Step 4: Convert the modified array back to JSON
        let newArrayJSON = JSON.stringify(existingArray);

        // Step 5: Update the localStorage item
        await localStorage.setItem("favs", newArrayJSON);
        await setStoredFavourites(JSON.parse(localStorage.getItem("favs")))
        setUrl("")
        setName("")
        closeFavs()
    }


    return (

        <>

            <div id="add-favs" className="add-favs">

                <div className="add-favs-box">
                    <div className="add-favs-head">
                        <p className="head-add">Add site</p>
                        <img className="close" onClick={closeFavs} src={close} />
                    </div>
                    <form onSubmit={onSubmitFavs} className="add-fav-form">
                        <input required type="text" className="add-fav-inp" placeholder="Website Name" value={name} onChange={(e) => { setName(e.target.value) }} />
                        <input required type="text" className="add-fav-inp" placeholder="Website URL" value={url} onChange={(e) => { setUrl(e.target.value) }} />
                        <input type="submit" className="add-fav-sub" value="Add site" />
                    </form>
                </div>
            </div>



            <div className="Favs">
                <div className="fav-box">
                    {
                        storedFavorites && storedFavorites.map((sf) => (
                            <div className="fav-show">
                                <a href={sf.url}>
                                    <div name={sf.name} className="fav" data-tip={sf.name} >
                                        <div className="fav-img-box">
                                            <img className="fav-img" src={webb} />
                                        </div>
                                        <p name={sf.name} data-tip={sf.name} className="fav-name">{sf.name}</p>
                                    </div>
                                </a>
                                <img className="penc" src={penc} />
                            </div>
                        ))
                    }
                    {
                        storedFavorites && storedFavorites.length > 9
                            ?
                            null
                            : <div onClick={openFavs} className="fav">
                                <div className="fav-img-box">
                                    <img className="fav-img pi" src={plus} />
                                </div>
                                <p className="fav-name">Add site</p>
                            </div>
                    }


                </div>
            </div>
        </>
    )
}


export default Favs